import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { typeDisciplineBehaviors } from 'dump/data';

const columns = [
  {
    title: 'Ký hiệu',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Tên hành vi',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      typeDisciplineBehaviors.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#typeDisciplineBehaviors/form/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListTypeDisciplineBehaviorsPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Hành vi kỷ luật">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/typeDisciplineBehaviors/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={typeDisciplineBehaviors} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeDisciplineBehaviorsPage />
  </Page>
);



